@import "https://fonts.googleapis.com/css2?family=Notable&display=swap";
* {
  box-sizing: border-box;
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

body {
  background-color: #d1afaf;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 9px;
}

.photo {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.card {
  width: 200px;
  text-align: center;
  transition: transform var(--animation-duration) var(--timing-function);
  background-color: #e3b8b8;
  border-radius: 15px;
  flex-direction: column;
  display: flex;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.card:hover {
  transform: scale(1.03);
}

.card > img {
  height: 170px;
  border-radius: 15px 15px 5px 5px;
}

.card__info {
  margin-top: 11px;
  display: flex;
}

.card__item {
  margin: auto;
}

.search-form {
  background-color: #b49999;
  justify-content: center;
  gap: 10px;
  margin-bottom: 15px;
  padding: 10px 0;
  display: flex;
}

input {
  border: none;
  border-radius: 15px;
  outline: none;
  padding: 10px 150px 10px 10px;
  font-size: 15px;
}

@media screen and (max-width: 450px) {
  input {
    padding: 10px 40px 10px 10px;
  }
}

button {
  cursor: pointer;
  text-transform: uppercase;
  border: none;
  border-radius: 15px;
  padding: 10px;
}

#load-more {
  color: #fff;
  background-color: #cb7373;
  border-radius: 15px;
  margin: 30px auto 10px;
  display: flex;
}

#load-more:hover {
  color: #fff;
  background-color: #f68888;
  box-shadow: 0 2px 2px rgba(0, 0, 0, .15);
}

input[type="submit"], button {
  color: #fff;
  cursor: pointer;
  background-color: #cb7373;
  border: none;
  transition-property: background-color, fill;
  transition-duration: .5s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

input[type="submit"], button:hover {
  color: #fff;
  background-color: #f68888;
  box-shadow: 0 2px 2px rgba(0, 0, 0, .15);
}

#load-more:disabled {
  cursor: not-allowed;
  background-color: #f2a5a5;
}

#load-more.hidden, .is-hidden {
  display: none;
}

